import ReactGA from "react-ga";
import history from "../history";

export const initGA = () => {
  console.log("GA Init");
  ReactGA.initialize("G-30NZFQVBDR"); // Aqui pones tu identificador de cuenta de Google Analytics
  history.listen((location) => ReactGA.pageview(location.pathname));
};

export const logPageView = () => {
  console.log("Page Log");
  ReactGA.set({ page: window.location.pathname }); // Aqui pones tu identificador de cuenta de Google Analytics
  ReactGA.pageview(window.location.pathname);
};
