import React, { Component } from "react";
import { connect } from "react-redux";
import Moment from "moment";
import "moment-timezone";
import "moment/locale/es";

import "../assets/styles.css";

import Loader from "../general/loader";

import { fetchReview } from "../../actions";

class Review extends Component {
  componentDidMount() {
    const { id } = this.props.match.params;
    this.props.fetchReview(id);
    window.scrollTo(0, 0);
  }

  renderReview() {
    if (!this.props.review) {
      return (
        <section className="page-section bg-light" id="portfolio">
          <div className="container">
            <Loader message="Loading.." />
          </div>
        </section>
      );
    } else {
      const {
        title,
        subtitle,
        image,
        impressions,
        experience,
        date_publish,
      } = this.props.review;
      const reviewDate = Moment(date_publish, "YYYY-MM-DD").format("LL");
      return (
        <section className="page-section" id="portfolio">
          <div className="review container">
            <div className="text-center">
              <h2 className="section-heading text-uppercase">{title}</h2>
              <h3 className="section-subheading text-muted">{subtitle}</h3>
              <hr />
              {/* Date/Time */}
              <p>Publicado el {reviewDate}</p>
              <hr />

              {/* Preview Image */}
              <div className="ui fluid image">
                <div className="ui red ribbon label">
                  <i className="tree icon"></i> Parque Nacional
                </div>
                <img src={image} alt="Chirripo" />
              </div>

              {/* Post Content */}

              <h4 className="ui horizontal divider header">
                <i className="pencil alternate icon"></i>
                Experiencia
              </h4>
              <div className="text-left">
                <p>{experience}</p>
                <blockquote>
                  <p className="mb-0">
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                    Integer posuere erat a ante.
                  </p>
                  <footer className="blockquote-footer">
                    Someone famous in{" "}
                    <cite title="Source Title">Source Title</cite>
                  </footer>
                </blockquote>
                <h4 className="ui horizontal divider header">
                  <i className="heart icon"></i>
                  Impresiones finales
                </h4>
                <p className="text-justify">{impressions}</p>
              </div>
            </div>
          </div>
        </section>
      );
    }
  }

  render() {
    return <div>{this.renderReview()}</div>;
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    review: state.reviews[ownProps.match.params.id],
  };
};

export default connect(mapStateToProps, { fetchReview })(Review);
